<template>
  <div class="pa-6">
    <v-card dark>
      <v-card-title class="headline primary--text">
        <v-icon class="mr-2 primary--text" small>fas fa-user</v-icon>
        Personal Information
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text v-if="profile.role">
        <v-row>
          <v-col cols="12" md="4" sm="12" class="pa-15">
            <div class="d-flex justify-center w-full align-center h-full">
              <v-avatar :class="roleColor(profile.role.name)" size="180" class="mb-10">
                <span v-if="profile.first_name && ! profile.avatar" class="white--text text-h2">
                  {{ profile.first_name.substr(0, 1) + profile.last_name.substr(0, 1) }}
                </span>
                <img
                  v-else alt="avatar"
                  :class="borderColorByRole(profile.role.name)"
                  :src="profile.avatar"
                />
              </v-avatar>
            </div>
          </v-col>
          <v-col cols="12" md="8" sm="12" class="pa-15">
            <div class="my-2">
              <strong>Name:</strong> {{profile.full_name}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>Username:</strong> {{profile.username}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>email:</strong> {{profile.email}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>Address:</strong> {{profile.address}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>Mobile Number:</strong> {{profile.mobile_no}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>BDO Account:</strong> {{profile.bdo_account}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>Security Bank Account:</strong> {{profile.security_bank_account}}
            </div>
            <v-divider></v-divider>
            <div class="my-2">
              <strong>Eastwest Account:</strong> {{profile.eastwest_account}}
            </div>
            <v-divider></v-divider>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  data: () => ({
  }),
  mounted() {
  },
  computed: {
    ...mapGetters({
      profile: 'profile',
      profileDialog: 'profileDialog'
    })
  },
  methods: {
  }
}
</script>
